___CSS_LOADER_EXPORT___.push([module.id, `@media (max-width: 600px) {
  .app-bar .app-bar__navigation {
    background-color:  #fff; /* Menetapkan latar belakang putih untuk mode responsif/ponsel */
  }
  #navigationDrawer ul li a {
    height: 45px;
    padding-bottom: 0px;
  }

  
}

@media only screen and (max-width: 600px) {
  .hero-container {
    width: 100%; 
    height: auto;
    max-height: calc(100vh - 100px); 
    object-fit: cover;
    margin-top: -100px; 
    margin-left: 0%; 
    transform: translateX(0%); 
  }
  
  .hero-slider {
    width: 100%; 
  }
  
  .hero-slider img {
    width: 100%; 
    object-fit: cover; 
    padding: 0 0px;
  }
}

@media screen and (min-width: 650px) {
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
   
    .app-bar .app-bar__brand h1 {
      font-size: 1.5em;
    }
   
    .app-bar .app-bar__menu {
      display: none;
    }
   
    .app-bar .app-bar__navigation {
      position: static;
      width: 100%;
      background-color: #fff;
    }
   
    .app-bar .app-bar__navigation ul li {
      display: inline-block;
    }
   
    .app-bar .app-bar__navigation ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
    }
    .restaurant__title {
      text-align: center;
    }
   
    .movies {
      grid-template-columns: 1fr 1fr;
    }
   
    .movie {
      grid-template-columns: auto 1fr;
    }
   
    .movie .movie__title {
      grid-column-start: 1;
      grid-column-end: 3;
    }
   
    .movie .movie__overview {
      grid-column-start: 1;
      grid-column-end: 3;
    }

    #hamburgerButton {
      width: 50px;
      height: 50px;
      font-size: 24px; 
      background-color: transparent; 
      border: none; 
      cursor: pointer; 
      display: flex; 
      align-items: center; 
      justify-content: center; 
      padding: 0; 
    }

    #hamburgerButton:hover {
      background-color: #f0f0f0; 
    }
  }
   
  @media screen and (min-width: 800px) {
    .app-bar .app-bar__brand h1 {
      font-size: 2em;
    }
  }
   
  @media screen and (min-width: 850px) {
    .movies {
      grid-template-columns: repeat(3, 1fr);
    }
  }
   
  @media screen and (min-width: 1200px) {
    .movies {
      grid-template-columns: repeat(4, 1fr);
    }
  }
   
  @media screen and (min-width: 1600px) {
    .movies {
        grid-template-columns: repeat(5, 1fr);
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,uBAAuB,EAAE,gEAAgE;EAC3F;EACA;IACE,YAAY;IACZ,mBAAmB;EACrB;;;AAGF;;AAEA;EACE;IACE,WAAW;IACX,YAAY;IACZ,+BAA+B;IAC/B,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,yBAAyB;EAC3B;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,WAAW;IACX,iBAAiB;IACjB,cAAc;EAChB;AACF;;AAEA;IACI;MACE,+BAA+B;MAC/B,iBAAiB;IACnB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,aAAa;IACf;;IAEA;MACE,gBAAgB;MAChB,WAAW;MACX,sBAAsB;IACxB;;IAEA;MACE,qBAAqB;IACvB;;IAEA;MACE,qBAAqB;MACrB,YAAY;MACZ,kBAAkB;MAClB,SAAS;IACX;IACA;MACE,kBAAkB;IACpB;;IAEA;MACE,8BAA8B;IAChC;;IAEA;MACE,+BAA+B;IACjC;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;;IAEA;MACE,WAAW;MACX,YAAY;MACZ,eAAe;MACf,6BAA6B;MAC7B,YAAY;MACZ,eAAe;MACf,aAAa;MACb,mBAAmB;MACnB,uBAAuB;MACvB,UAAU;IACZ;;IAEA;MACE,yBAAyB;IAC3B;EACF;;EAEA;IACE;MACE,cAAc;IAChB;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;QACI,qCAAqC;IACzC;EACF","sourcesContent":["@media (max-width: 600px) {\r\n  .app-bar .app-bar__navigation {\r\n    background-color:  #fff; /* Menetapkan latar belakang putih untuk mode responsif/ponsel */\r\n  }\r\n  #navigationDrawer ul li a {\r\n    height: 45px;\r\n    padding-bottom: 0px;\r\n  }\r\n\r\n  \r\n}\r\n\r\n@media only screen and (max-width: 600px) {\r\n  .hero-container {\r\n    width: 100%; \r\n    height: auto;\r\n    max-height: calc(100vh - 100px); \r\n    object-fit: cover;\r\n    margin-top: -100px; \r\n    margin-left: 0%; \r\n    transform: translateX(0%); \r\n  }\r\n  \r\n  .hero-slider {\r\n    width: 100%; \r\n  }\r\n  \r\n  .hero-slider img {\r\n    width: 100%; \r\n    object-fit: cover; \r\n    padding: 0 0px;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n    .app-bar {\r\n      grid-template-columns: 1fr auto;\r\n      padding: 8px 32px;\r\n    }\r\n   \r\n    .app-bar .app-bar__brand h1 {\r\n      font-size: 1.5em;\r\n    }\r\n   \r\n    .app-bar .app-bar__menu {\r\n      